import React from "react";
import { Breadcrumb, BreadcrumbItem } from "reactstrap";
// Import React Table
import { useTable, usePagination } from "react-table";

const data = [
  { firstName: "a", lastName: "doe", age: 20 },
  { firstName: "b", lastName: "smith", age: 21 },
  { firstName: "c", lastName: "smith", age: 21 },
  { firstName: "d", lastName: "smith", age: 21 },
  { firstName: "e", lastName: "smith", age: 21 },
  { firstName: "f", lastName: "smith", age: 21 },
  { firstName: "g", lastName: "smith", age: 21 },
  { firstName: "h", lastName: "smith", age: 21 },
  { firstName: "i", lastName: "smith", age: 21 },
  { firstName: "j", lastName: "smith", age: 21 },
  { firstName: "k", lastName: "smith", age: 21 },
  { firstName: "l", lastName: "smith", age: 21 },
  { firstName: "m", lastName: "smith", age: 21 },
  { firstName: "n", lastName: "smith", age: 21 },
  { firstName: "o", lastName: "smith", age: 21 },
  { firstName: "p", lastName: "smith", age: 21 },
];

const columns = [
  {
    Header: "Name",
    columns: [
      {
        Header: "First Name",
        accessor: "firstName",
      },
      {
        Header: "Last Name",
        accessor: "lastName",
      },
    ],
  },
  {
    Header: "Other Info",
    columns: [
      {
        Header: "Age",
        accessor: "age",
      },
    ],
  },
];

const Table = ({ columns, data }) => {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    page,
    prepareRow,
    canPreviousPage,
    canNextPage,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0 },
    },
    usePagination
  );

  return (
    <>
      <table className="table table-bordered table-hover" {...getTableProps()}>
        <thead className="thead-light">
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>{column.render("Header")}</th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row, i) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
      <ul className="pagination justify-content-end">
        <li className="page-item">
          <button
            className="page-link"
            onClick={() => gotoPage(0)}
            disabled={!canPreviousPage}
          >
            {"<<"}
          </button>
        </li>
        <li className="page-item">
          <button
            className="page-link"
            onClick={() => previousPage()}
            disabled={!canPreviousPage}
          >
            {"<"}
          </button>
        </li>
        <li className="page-item">
          <button
            className="page-link"
            onClick={() => nextPage()}
            disabled={!canNextPage}
          >
            {">"}
          </button>
        </li>
        <li className="page-item">
          <button
            className="page-link"
            onClick={() => gotoPage(pageCount - 1)}
            disabled={!canNextPage}
          >
            {">>"}
          </button>
        </li>
      </ul>
    </>
  );
};

function Customers() {
  return (
    <>
      <Breadcrumb>
        <BreadcrumbItem active>Customers</BreadcrumbItem>
      </Breadcrumb>
      <div>
        <Table columns={columns} data={data} />
      </div>
    </>
  );
}

export default Customers;
